import React, { Component } from 'react'
import axios from 'axios';

export default class SpotList extends Component {

    constructor(){
        super()
        this.state = {
            spots: []
        }
    }

    componentDidMount() {
        axios.get(`http://s3.eu-west-2.amazonaws.com/lpad-public-assets/software-test/all-spots.json`)
        .then(res => {
            this.setState({
                spots: res.data
            });
        });
    }

    render() {
        return (
            <ul>
                {this.state.spots.map (spot => <li>{spot.county_name}</li>)}
            </ul>
        )
    }
}
